html,
body {
  height: 100%;
  
}

body {
  margin: 0;
  font-family: Montserrat, -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: radial-gradient(
    circle at top right,
    #0c0134 0%,
    #0d0128 25%
  );
  background-position: 100% 0;
  background-position-x: 100%;
  background-position-y: 0px;
  background-repeat: no-repeat;
  background-image: url(/src/assets/royalbg.d6a060b8.webp);
  background-size: cover;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
  overflow: auto;
}

.scrollbox {
  overflow-y: scroll;
}

.scrollbox::-webkit-scrollbar {
  background-color: transparent;
  width: 8px;
}

.scrollbox::-webkit-scrollbar-thumb {
  background-color: #F5BD02;
  border-radius: 4px;
}

a {
  color: rgb(222, 215, 219);
}


.clock {
  position: relative;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(270deg, #051817, #0c2222);
  height: 130px;
  width: 260px;
  color: #F5BD02;
  text-shadow: 1px 1px 7px;
  border-radius: 15px;
  border: 2px solid silver;
  box-shadow: 0 0 5px 2px;
}

.buttonclock {
  position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 34px;
  width: 150px;
  color: #f6f5f2;
  text-shadow: 1px 1px 7px;
}

.clock section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}
.clock section p {
  font-size: 2rem;
}
.clock section small {
  color: silver;
  text-shadow: none;
}

/** tooltip start here**/
.tooltip {
  position: center;
  display: flex;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 100%;
  background-color: rgb(24, 25, 26);
  color: rgb(255, 255, 255);
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 150%;
  /* left: 50%;
  margin-left: -75px; */
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

/** tooltip end here **/

@font-face {
  font-family: "Light";   /*Can be any text*/
  src: local("TESLA"),
    url("./assets/TESLA.ttf") format("truetype");
}

